<template>
    <Transition name="toast-fade">
        <div class="container" v-if="localIsActive" @click="hideToast">

            <div v-if="localIsActive" class="toast">
                {{ message }}
            </div>

        </div>
    </Transition>
</template>

<script setup>
import { ref, watch } from 'vue'

//定义props
const props = defineProps({
    message: '',
    isActive: false
})
const localIsActive = ref(props.isActive)
watch(() => props.isActive, (newValue) => {
    if (newValue) {
        localIsActive.value = newValue;
        setTimeout(() => {
            localIsActive.value = false; // 修改本地 ref
            emit('close', 1)
        }, 1500);
    }
},
    // { flush: 'sync' }
)

//定义emit
const emit = defineEmits(['close', 'feedback'])
function hideToast() {
    localIsActive.value = false
    emit('close', 123)
}

</script>

<style scoped>
.toast-fade-enter-active {
    transition: all 0.1s ease-in;
}

.toast-fade-leave-active {
    transition: all 0.2s ease-out;
}

.toast-fade-enter-from,
.toast-fade-leave-to {
    transform: scale(0);
}

.toast-fade-enter-to,
.toast-fade-leave-from {
    transform: scale(1);
}

.toast {
    width: 70%;
    height: 60px;
    line-height: 60px;
    text-align: center;
    word-wrap: break-word;
    overflow: hidden;
    text-align: center;
    line-clamp: 2;
    text-overflow: ellipsis;
    background-color: rgba(0, 0, 0, 0.8);
    color: white;
    border-radius: 11px;
    font-size: 16px;
}

.container {
    position: absolute;
    left: 0;
    top: 0;
    z-index: 10001;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0);
    display: flex;
    justify-content: center;
    align-items: center;
    /* opacity: 0.5; */
}
</style>